import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import composeIcon from '../images/compose.png';
import sendIcon from '../images/send.png';
import { getCookie } from '../utils/cookie';
import { fetchReducer, initialPageState, useThunkReducer } from '../utils/fetch';
import mainApi from '../utils/MainApi';

function Chats() {
  const [state, thunkDispatch] = useThunkReducer(fetchReducer, initialPageState);
  const navigate = useNavigate();
  const [allChatsData, setAllChatsData] = useState([]);
  const [chatMessages, setChatMessages] = useState([]);
  const [messageInput, setMessageInput] = useState('');
  const handleKeyPress = (event) => {
    if (event.key.toLowerCase() === 'enter') handleSubmit(event);
  };

  const handleChange = (event) => {
    setMessageInput(event.target.value);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    const friendId = event.target.closest('form').id;
    mainApi
      .sendMessage(thunkDispatch, messageInput, friendId)
      .then((response) => console.log(response));
  };
  const handleFriendChatClick = (event) => {
    const ws = new WebSocket('ws://localhost:3000');
    ws.onopen = () => {
      ws.send('hey');
    };
    ws.onmessage = (message) => console.log(message.data);
    let friendTarget = event.target;
    const checkTarget = (target) => {
      if (target.className.indexOf('chats__chat-link') !== -1) {
        friendTarget = target;
        return;
      }
      checkTarget(target.parentNode);
    };
    checkTarget(friendTarget);
    mainApi.getMessages(thunkDispatch, friendTarget.id).then((result) => {
      console.log(result);
      setChatMessages(result);
    });
  };
  const handleBack = () => navigate('/');
  useEffect(() => {
    mainApi
      .signOut(thunkDispatch)
      .then(() => {
        mainApi.signIn(thunkDispatch, 'yaniv@me.com', 'Yaniv12').then(() => {
          mainApi.getChats(thunkDispatch).then((result) => setAllChatsData(result));
        });
      })
      .catch(() =>
        mainApi.signIn(thunkDispatch, 'yaniv@me.com', 'Yaniv12').then(() => {
          mainApi.getChats(thunkDispatch).then((result) => setAllChatsData(result));
        })
      );
  }, []);
  return (
    <div className="chats">
      <div className="chats__contacts">
        <div className="chats__contacts-top">
          <img
            src={composeIcon}
            width="50"
            height="50"
            className="chats__compose-icon"
            alt="compose icon"
          />

          <h1 className="chats__title">Chats</h1>

          <div className="chats__back-button" onClick={handleBack}>
            <p className="chats__back-title">Back</p>
          </div>
        </div>

        <div className="chats__contacts-main">
          {allChatsData.map(
            ({
              friendId,
              friendName,
              friendImage,
              isMute,
              lastMessage,
              lastMessageTime,
              unreedMessages,
            }) => (
              <div
                className="chats__chat-link"
                key={friendId}
                id={friendId}
                onClick={handleFriendChatClick}
              >
                <img className="chats__friend-icon" src={friendImage} alt="friend icon" />

                <div className="chats__chat-row">
                  <div className="chats__chat-column">
                    <p className="chats__chat-title">{friendName}</p>

                    <p className="chats__chat-message">{lastMessage}</p>
                  </div>

                  <div className="chats__chat-end-column">
                    <p className="chats__chat-time">{lastMessageTime}</p>

                    {isMute ? (
                      <img className="chats__chat-mute" src="/mute.png" alt="chat mute" />
                    ) : null}

                    {unreedMessages > 0 ? (
                      <div className="chats__chat-unread">
                        <p className="chats__chat-unread-count">{unreedMessages}</p>
                      </div>
                    ) : null}
                  </div>
                </div>
              </div>
            )
          )}
        </div>
      </div>

      <div className="chats__chat-messages-container">
        {chatMessages.messages ? (
          <>
            <div className="chats__messages-header" key={chatMessages.friendId}>
              <img
                src={chatMessages.friendImage}
                className="chats__friend-header-icon"
                alt="friend icon"
              />

              <h2 className="chats__friend-header-name">{chatMessages.friendName}</h2>
            </div>

            <div id="messagesContainer" className="chats__messages-container">
              {chatMessages.messages.length > 0
                ? chatMessages.messages
                    .slice(0)
                    .reverse()
                    .map(({ messageByUser, messageContent, messageTime, messageDate }) => {
                      if (messageByUser)
                        return (
                          <div className="chats__message chats__message_user">
                            <p className="chats__message-text">{messageContent}</p>

                            <p className="chats__message-date">{messageTime}</p>
                          </div>
                        );
                      else
                        return (
                          <div className="chats__message chats__message_friend">
                            <p className="chats__message-text">{messageContent}</p>

                            <p className="chats__message-date">{messageTime}</p>
                          </div>
                        );
                    })
                : ''}
            </div>

            <form
              className="chats__send-form"
              name="message"
              onSubmit={handleSubmit}
              id={chatMessages.friendId}
            >
              <textarea
                className="chats__message-input"
                value={messageInput}
                onChange={handleChange}
                onKeyPress={handleKeyPress}
              ></textarea>

              <button type="submit" className="chats__send-button">
                <img src={sendIcon} className="chats__send-icon" alt="send-icon" />
              </button>
            </form>
          </>
        ) : (
          ''
        )}
      </div>
    </div>
  );
}

export default Chats;
