import backEndApi from '../assets/backEndApi';
import { useFetch } from './fetch';

class MainApi {
  constructor(baseUrl) {
    this.baseUrl = baseUrl;
  }

  signUp = (dispatch, email, password, name) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/signup`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email,
          password,
          name,
        }),
      },
      { silent: true }
    ).then((response) => response);

  signIn = (dispatch, email, password) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/signin`,
      {
        method: 'POST',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email,
          password,
        }),
      },
      { silent: true }
    ).then((response) => response);

  signOut = (dispatch) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/signout`,
      {
        method: 'POST',
        credentials: 'include',
      },
      { silent: true }
    ).then((response) => response);

  getUserMe = (dispatch) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/users/me`,
      {
        credentials: 'include',
      },
      { auth: true }
    ).then((response) => response);

  getChats = (dispatch) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/chats`,
      {
        credentials: 'include',
      },
      { silent: true }
    ).then((response) => response);

  getMessages = (dispatch, friendId) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/messages/${friendId}`,
      {
        credentials: 'include',
      },
      { silent: true }
    ).then((response) => response);

  sendMessage = (dispatch, message, friendId) =>
    useFetch(
      dispatch,
      `${this.baseUrl}/messages/${friendId}`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ message }),
        credentials: 'include',
      },
      { silent: true }
    ).then((response) => response);
}

const mainApi = new MainApi(backEndApi);

export default mainApi;
